}

/* Start Dialog Page */
.start-game-dialog {
    width: 44rem;
    min-height: 22rem;
    padding-bottom: 5rem;
    border: 2px solid var(--font-default);
    border-radius: 4px;
    position: fixed;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--bg);
}

.start-game-dialog[open] {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.start-game-dialog::backdrop {
    background-color: var(--bg);
    opacity: 1;
}

.dialog-one-title {
    font-family: "Black Ops One";
    color: var(--font-default);
    font-size: 4rem;
}

/* Game Form Styling */
.game-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.form-header {
    color: var(--font-default);
}

#input-player-1,
#input-player-2 {
    background-color: var(--bg);
    border: 1px solid var(--font-default);
    border-radius: 5px;
    color: var(--font-default);
    padding-left: 1rem;
    transition: all 0.3s ease;
}

#input-player-1::placeholder,
#input-player-2::placeholder {
    color: var(--font-default);
}

#input-player-1:focus,
#input-player-2:focus {
    outline: none;
}

.radio-label {
    color: var(--font-default);
}

.radio-buttons {
    border: 1px solid var(--font-default);
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.radio-label {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

input[type="radio"] {
    appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid var(--font-default);
    border-radius: 50%;
    display: inline-block;
    position: relative;
    cursor: pointer;
}

#single-player:checked,
#two-player:checked {
