lay=swap\");\n\n@font-face {\n    font-family: \"OCR A Extended\";\n    src: url(./fonts/OCRAEXT.TTF);\n    font-weight: normal;\n    font-style: normal;\n}\n\n@font-face {\n    font-family: \"Impact\";\n    src: url(./fonts/impact.woff);\n    font-weight: normal;\n    font-style: normal;\n}\n\n/* CSS Reset */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n}\n\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n    display: block;\n    max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    overflow-wrap: break-word;\n}\n\np {\n    text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    text-wrap: balance;\n}\n\n#root,\n#__next {\n    isolation: isolate;\n}\n/* Reset Ends */\n\n:root {\n    --font-default: #33fd33;\n    --green: #004300;\n    --bg: #062306;\n    --bg-dark: #041604;\n    --red: #c72c23;\n    --grey: #586457;\n}\n\nbody {\n    width: 100%;\n    min-width: 60rem;\n    min-height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    font-family: \"OCR A Extended\", \"Impact\", Arial, Helvetica, sans-serif;\n    background-color: var(--bg);\n}\n\n/* Start Dialog Page */\n.start-game-dialog {\n    width: 44rem;\n    min-height: 22rem;\n    padding-bottom: 5rem;\n    border: 2px solid var(--font-default);\n    border-radius: 4px;\n    position: fixed;\n    top: 35%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    background-color: var(--bg);\n}\n\n.start-game-dialog[open] {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\n.start-game-dialog::backdrop {\n    background-color: var(--bg);\n    opacity: 1;\n}\n\n.dialog-one-title {\n    font-family: \"Black Ops One\";\n    color: var(--font-default);\n    font-size: 4rem;\n}\n\n/* Game Form Styling */\n.game-form {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n}\n\n.form-header {\n    color: var(--font-default);\n}\n\n#input-player-1,\n#input-player-2 {\n    background-color: var(--bg);\n    border: 1px solid var(--font-default);\n    border-radius: 5px;\n    color: var(--font-default);\n    padding-left: 1rem;\n    transition: all 0.3s ease;\n}\n\n#input-player-1::placeholder,\n#input-player-2::placeholder {\n    color: var(--font-default);\n}\n\n#input-player-1:focus,\n#input-player-2:focus {\n    outline: none;\n}\n\n.radio-label {\n    color: var(--font-default);\n}\n\n.radio-buttons {\n    border: 1px solid var(--font-default);\n    border-radius: 5px;\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n}\n\n.radio-label {\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n}\n\ninput[type=\"radio\"] {\n    appearance: none;\n    width: 20px;\n    height: 20px;\n    border: 2px solid var(--font-default);\n    border-radius: 50%;\n    display: inline-block;\n    position: relative;\n    cursor: pointer;\n}\n\n#single-player:checked,\n#two-player:checked {\n    background-color: var(--font-default);\n}\n\n#start-button {\n    color: var(--font-default);\n    background-color: var(--bg);\n    border: 1px solid var(--font-default);\n    border-radius: 5px;\n    cursor: pointer;\n    transition: all 0.3s ease;\n    font-weight: bold;\n}\n\n#start-button:hover {\n    color: var(--bg);\n    background-color: var(--font-default);\n    font-weight: bold;\n}\n\n/* Place Ships One Dialog */\n.place-ships-one {\n    border: none;\n    width: 100vw;\n    min-width: 60rem;\n    min-height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 0;\n    background-color: var(--bg);\n}\n\n.place-ships-one:not([open]) {\n    display: none;\n}\n\n.place-ships-one::backdrop {\n    background-color: var(--bg);\n    opacity: 1;\n}\n\n.dialog-one-header {\n    width: 20rem;\n    min-height: 5rem;\n    border: 1px solid var(--font-default);\n    border-radius